<template>
  <div>
    <span class="subheading font-weight-medium">Card</span>
    <el-divider direction="vertical" />
    <el-link :disabled="!items.length" :underline="false" type="info" @click="exportExcel">
      Export
      <i class="el-icon-printer el-icon--right"></i> 
    </el-link>

    <el-table
      v-if="items.length"
      :data="items"
      border
      size="mini"
      class="mt-2"
      style="width: 100%">
      <el-table-column 
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :prop="c.prop"
        :align="c.align"
        :sortable="c.sortable">
        <template slot="header" scope="{ column }">
          <span :title="column.label">{{ column.label }}</span>
        </template>
        <template slot-scope="scope">
          <span v-if="['LIMIT_AMT'].includes(c.prop)">
            {{ scope.row[c.prop] | numberWithDots }}
          </span>
          <span v-else>{{ scope.row[c.prop] }}</span>
        </template>
      </el-table-column>
    </el-table>
    <div 
      v-else 
      class="text-md-center text-sm-center text-xs-center subheading ma-2">
      No data available
    </div>
  </div>
</template>
<script>
import Export from '@/mixins/export.js'

export default {
  name: 'T24-card',

  mixins: [Export],

  props: {
    items: {
      type: Array,
      default: () => []
    }
  },

  data: () => ({
    columnDefs: [
      { label: 'Card key', prop: 'CARD_KEY', width: 100 },
      { label: 'IDKH', prop: 'CUSTOMER', width: 80 },
      { label: 'Tên KH', prop: 'NAME', width: 150 },
      { label: 'Số tài khoản', prop: 'ACCOUNT_', width: 120 },
      { label: 'Loại thẻ', prop: 'LOAI_THE', width: 65 },
      { label: 'Trạng thái', prop: 'TRANG_THAI', width: 65 },
      { label: 'Ngày yêu cầu', prop: 'NGAY_YEU_CAU', width: 85 },
      { label: 'Ngày phát hành', prop: 'NGAY_PHAT_HANH', width: 85 },
      { label: 'Ngày nhận thẻ', prop: 'NGAY_NHAN_THE', width: 85 },
      { label: 'Mô tả', prop: 'DISCRIPTION', width: 200 },
      { label: 'Loại KH', prop: 'LOAI_KH', width: 140 },
      { label: 'Loại PH', prop: 'LOAI_PH', width: 100 },
      { label: 'Mã hợp đồng', prop: 'CONTRACT_ID', width: 120 },
      { label: 'Hạn mức', prop: 'LIMIT_AMT' },
      // { label: 'Acc auto debit', prop: 'ACCT_AUTO_DEBIT' },
      // { label: 'Acc transfer', prop: 'ACCT_TRANSFER' },
      // { label: 'Acc name', prop: 'ACCT_NAME' },
      // { label: 'Min pay', prop: 'MIN_PAY' },
      { label: 'KH trả lương', prop: 'KH_TRA_LUONG' },
      { label: 'CMT chủ thẻ', prop: 'CMT_CHU_THE', width: 100 },
      // { label: 'VNA ID', prop: 'VNA_ID' },
      // { label: 'Bảo hiểm', prop: 'BAO_HIEM' },
      { label: 'Hình thức bảo đảm', prop: 'H_THUC_BAO_DAM' },
      // { label: 'PH nhanh', prop: 'PHAT_HANH_NHANH' },
      // { label: 'Loại thay thế', prop: 'LOAI_THAY_THE' },
      // { label: 'Số thẻ Mask', prop: 'SO_THE_MASK' },
      { label: 'CTY trả lương', prop: 'CTY_TRALUONG' }
    ]
  }),

  methods: {
    exportExcel() {
      const fields = this.columnDefs.map(e => e.prop)
      const params = {
        header: fields,
        filterVal: fields,
        filename: `CARD-${this.items[0].CUSTOMER}`,
        items: this.items
      }
      this.handleDownload(params)
    }
  }
}
</script>



