<template>
  <div>
    <span class="subheading font-weight-medium">Transaction</span>
    <el-divider direction="vertical" />
    <el-link v-if="items.length" :underline="false" type="info" @click="exportExcel">
      Export
      <i class="el-icon-printer el-icon--right"></i> 
    </el-link>

    <el-table
      :data="entries"
      border
      size="mini"
      class="mt-2"
      style="width: 100%">
      <el-table-column type="index" width="45" align="center" :index="generateIndex" />
      <el-table-column 
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :prop="c.prop"
        :align="c.align"
        :sortable="c.sortable">
        <template slot="header" scope="{ column }">
          <span :title="column.label">{{ column.label }}</span>
        </template>
        <template slot-scope="scope">
          <span v-if="c.prop === 'BOOKING_DATETIME'">
            {{ scope.row[c.prop] | cleanBookingDate }}
          </span>
          <span v-else-if="['DEBIT', 'CREDIT'].includes(c.prop)">
            {{ scope.row[c.prop] | numberWithDots }}
          </span>
          <span v-else>{{ scope.row[c.prop] | cleanLocalRef }}</span>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      :total="items.length"
      :page-sizes="[15, 50, 100]"
      :page-size="page_size"
      :current-page="page_number"
      class="mt-2 mb-3 right"
      layout="total, sizes, prev, pager, next, jumper"
      background
      @current-change="currentPageChange"
      @size-change="sizeChange">
    </el-pagination>
  </div>
</template>
<script>
import Export from '@/mixins/export.js'

export default {
  name: 'T24-transaction',

  mixins: [Export],

  props: {
    items: {
      type: Array,
      default: () => []
    }
  },

  mounted() {
    this.entries = this.items.slice(0, this.page_size)
  },

  watch: {
    items() {
      this.page_number = 1
      this.entries = this.items.slice(0, this.page_size)
    }
  },

  computed: {
    generateIndex() {
      if (this.page_number === 1) return this.page_number
      else return (this.page_number - 1) * this.page_size + 1
    }
  },

  data: () => ({
    columnDefs: [
      { label: 'Số tài khoản', prop: 'ACCOUNT_NUMBER', width: 120 },
      { label: 'Tên tài khoản', prop: 'ACCOUNT_NAME', width: 200 },
      { label: 'IDKH', prop: 'CUSTOMER_ID', width: 80 },
      { label: 'Loại tiền', prop: 'CURRENCY', width: 80 },
      // { label: 'Booking date', prop: 'BOOKING_DATE' },
      { label: 'Booking Date', prop: 'BOOKING_DATETIME', width: 130 },
      { label: 'Số bút toán', prop: 'ENTRY_NUMBER', width: 200 },
      { label: 'Ghi nợ', prop: 'DEBIT', width: 120 },
      { label: 'Ghi có', prop: 'CREDIT', width: 120 },
      { label: 'Nội dung', prop: 'DESCRIPTION' },
      { label: 'Loại giao dịch', prop: 'TRANSACTION_TYPE', width: 100 },
      { label: 'TK đối ứng', prop: 'CONTRA_ACCOUNT', width: 140 },
      { label: 'Tên TK đối ứng', prop: 'CONTRA_ACCOUNT_NAME', width: 180 }
    ],
    entries: [],
    page_size: 15,
    page_number: 1
  }),

  methods: {
    exportExcel() {
      const fields = this.columnDefs.map(e => e.prop)
      const params = {
        header: fields,
        filterVal: fields,
        filename: `TRANSACTION-${this.items[0].CUSTOMER_ID}`,
        items: this.items
      }
      this.handleDownload(params)
    },

    paginator() {
      const offset = (this.page_number - 1) * this.page_size
      const paginatedItems = this.items.slice(offset).slice(0, this.page_size)
      const total_pages = Math.ceil(this.items.length / this.page_size)

      return {
        page_number: this.page_number,
        page_size: this.page_size,
        pre_page: this.page_number - 1 ? this.age_number - 1 : null,
        next_page: (total_pages > this.page_number) ? this.page_number + 1 : null,
        total: this.items.length,
        total_pages: total_pages,
        items: paginatedItems
      }
    },

    sizeChange(value) {
      this.page_size = value
      this.entries = this.paginator().items
    },

    currentPageChange(value) {
      this.page_number = value
      this.entries = this.paginator().items
    }
  }
}
</script>



