<template>
  <el-dialog
    :visible.sync="dialogResultVisible"
    :before-close="TOGGLE_RESULT_DIALOG"
    width="30%"
    fullscreen>
    <template slot="title">
      <div class="tag-group">
        <span class="subheading font-weight-medium">Query result for </span>
        <template v-for="(v, k) in request.q">
          <el-tag
            v-if="v"
            :key="k"
            :type="fields[k].type"
            class="ml-2"
            effect="plain">
            <b>{{ fields[k].label }}</b>&nbsp;{{ v.replace('T00:00:00', '') || '&mdash;' }}
          </el-tag>
        </template>
      </div>
    </template>
    <template v-if="request.category === 'customer_info'">
      <BasicInformation :items="request.result.customer" class="mb-4" />
      <Card :items="request.result.card" class="mb-4" />
      <Limit :items="request.result.limit" class="mb-4" />
      <Mobilize :items="request.result.mobilize" class="mb-4" />
    </template>
    <Transaction 
      v-else-if="request.category === 'transaction'" 
      :items="request.result.transaction" 
      class="mb-4" />
  </el-dialog>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import BasicInformation from '@/components/QueryTool/Result/T24/BasicInformation'
import Card from '@/components/QueryTool/Result/T24/Card'
import Limit from '@/components/QueryTool/Result/T24/Limit'
import Mobilize from '@/components/QueryTool/Result/T24/Mobilize'
import Transaction from '@/components/QueryTool/Result/T24/Transaction'

export default {
  name: 'QT-result-dialog',

  props: {
    request: {
      type: Object,
      default: () => {}
    }
  },

  components: {
    BasicInformation,
    Card,
    Limit,
    Mobilize,
    Transaction
  },

  computed: {
    ...mapGetters('QueryTool', ['dialogResultVisible'])
  },

  data: () => ({
    fields: {
      customer_id: { label: 'Customer ID', type: 'danger' },
      legal_id: { label: 'Legal ID', type: 'info' },
      passport: { label: 'Passport', type: 'primary' },
      tax_code: { label: 'Tax code', type: 'warning' },
      account_number: { label: 'Account number', type: 'primary' },
      from: { label: 'From', type: 'info' },
      to: { label: 'To', type: 'info' }
    }
  }),

  methods: {
    ...mapMutations('QueryTool', ['TOGGLE_RESULT_DIALOG'])
  }
}
</script>
<style scoped>
  >>>.el-dialog__body {
    padding: 15px 20px;
  }
</style>



