<template>
  <div>
    <span class="subheading font-weight-medium">Limit</span>
    <el-divider direction="vertical" />
    <el-link :disabled="!items.length" :underline="false" type="info" @click="exportExcel">
      Export
      <i class="el-icon-printer el-icon--right"></i> 
    </el-link>

    <el-table
      v-if="items.length"
      :data="items"
      border
      size="mini"
      class="mt-2"
      style="width: 100%">
      <el-table-column 
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :prop="c.prop"
        :align="c.align"
        :sortable="c.sortable">
        <template slot="header" scope="{ column }">
          <span :title="column.label">{{ column.label }}</span>
        </template>
        <template slot-scope="scope">
          <span v-if="['online_limit', 'ost', 'available_limit'].includes(c.prop)">
            {{ scope.row[c.prop] | numberWithDots }}
          </span>
          <span v-else>{{ scope.row[c.prop] }}</span>
        </template>
      </el-table-column>
    </el-table>
    <div 
      v-else 
      class="text-md-center text-sm-center text-xs-center subheading ma-2">
      No data available
    </div>
  </div>
</template>
<script>
import Export from '@/mixins/export.js'

export default {
  name: 'T24-limit',

  mixins: [Export],

  props: {
    items: {
      type: Array,
      default: () => []
    }
  },

  data: () => ({
    columnDefs: [
      { label: 'Limit ID', prop: 'limit_id' },
      { label: 'IDKH', prop: 'customer_id' },
      { label: 'TK thấu chi', prop: 'overdraft_account' },
      { label: 'CN', prop: 'branch' },
      { label: 'Online limit', prop: 'online_limit' },
      { label: 'Hạn mức', prop: 'limit' },
      { label: 'Tổng dư nợ', prop: 'ost' },
      { label: 'Hạn mức còn lại', prop: 'available_limit' },
      { label: 'Ngày giải ngân', prop: 'disbursement_date' },
      { label: 'Ngày đáo hạn', prop: 'maturity_date' },
      { label: 'Sản phẩm', prop: 'product' },
      { label: 'Chương trình', prop: 'program' },
      { label: 'Chính sách', prop: 'policy' },
      { label: 'Mã MĐV', prop: 'purpose_code' },
      { label: 'Mô tả MĐV', prop: 'purpose_description' },
      { label: 'Mã hồ sơ', prop: 'application_id' },
      { label: 'Phê duyệt', prop: 'approver' }
    ]
  }),

  methods: {
    exportExcel() {
      const fields = this.columnDefs.map(e => e.prop)
      const params = {
        header: fields,
        filterVal: fields,
        filename: `LIMIT-${this.items[0].customer_id}`,
        items: this.items
      }
      this.handleDownload(params)
    }
  }
}
</script>



