<template>
  <div>
    <div class="heading">
      <h1 class="title">Requests</h1>
      <label class="search icon-before icon-search">
        <input v-model="q.search_key" type="text" placeholder="Search">
      </label>
    </div>
    <el-row :gutter="15" class="mb-3">
      <el-col :xs="24" :sm="filterSidebar ? 16 : 24" :md="filterSidebar ? 16 : 24" :lg="filterSidebar ? 20 : 24" :xl="filterSidebar ? 20 : 24">
        <!-- <el-card class="box-card" shadow="never"> -->
          <div class="right mr-1">
            <el-tooltip class="item" effect="dark" content="Refresh" placement="top-end">
              <el-button 
                :disabled="loading"
                size="mini" 
                icon="icon icon-spinner11" 
                type="text" 
                @click="onRefresh" />
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="Toggle filter" placement="top-start">
              <el-button 
                size="mini" 
                icon="icon icon-filter" 
                type="text" 
                @click="toggleFilter" />
            </el-tooltip>
          </div>
          <div class="left">
            <span class="primary--text">Auto update result every <b>60</b> seconds</span>
          </div>
          <el-table
            :data="$store.state.QueryTool.requests"
            v-loading="loading"
            element-loading-text="Loading..."
            size="mini"
            style="width: 100%"
            border>
            <el-table-column type="index" width="55" align="center" :index="generateIndex" />
            <el-table-column 
              v-for="(c, i) in columnDefs"
              :key="i"
              :label="c.label"
              :width="c.width"
              :align="c.align"
              :fixed="c.fixed"
              :sortable="c.sortable">
              <template slot="header" slot-scope="{ column }">
                <span :title="column.label">{{ column.label }}</span>
              </template>
              <template slot-scope="scope">
                <span 
                  v-if="['request_at', 'respond_at'].includes(c.prop)"
                  :title="scope.row[c.prop]">
                  {{ scope.row[c.prop] | moment('from', 'now') || '-' }}
                </span>
                <span v-else-if="['category'].includes(c.prop)">
                  <el-tag :type="categories[scope.row[c.prop]].type" size="small" effect="plain">
                    <b>{{ categories[scope.row[c.prop]].description }}</b>
                  </el-tag>
                </span>
                <span v-else-if="['status'].includes(c.prop)">
                  <el-tag 
                    v-if="isEmptyObj(scope.row.result) && scope.row[c.prop] === 1" 
                    type="danger" 
                    size="small"
                    effect="plain"> 
                    <b>No Data</b>
                  </el-tag>
                  <el-tag 
                    v-else
                    :type="status[scope.row[c.prop]].type" 
                    size="small" 
                    effect="plain">
                    <b>{{ status[scope.row[c.prop]].description }}</b>
                  </el-tag>
                </span>
                <span v-else>{{ accessObjByString(c.prop, scope.row) || '-' }}</span>
              </template>
            </el-table-column>
            <el-table-column width="80" fixed="right" label="Actions" align="center">
              <template slot-scope="scope">
                <el-button
                  :disabled="isEmptyObj(scope.row.result)"
                  :class="{ 'primary--text': !isEmptyObj(scope.row.result) }" 
                  :title="isEmptyObj(scope.row.result) ? 'No data' : ''"
                  type="text" 
                  size="small"
                  @click.native.prevent="view(scope.row)">
                  View
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-pagination
            v-if="$store.state.QueryTool.total > 0"
            :disabled="loading"
            :total="$store.state.QueryTool.total"
            :page-sizes="[20, 50, 100]"
            :page-size="q.page_size"
            :current-page="q.page_number"
            class="mt-2"
            layout="total, sizes, prev, pager, next, jumper"
            background
            @current-change="currentPageChange"
            @size-change="sizeChange">
          </el-pagination>
        <!-- </el-card> -->
      </el-col>

      <el-col v-if="filterSidebar" :xs="24" :sm="8" :md="8" :lg="4" :xl="4">
        <el-card class="box-card" shadow="never">
          <!-- <div slot="header" class="clearfix">
            <span class="subheading font-weight-medium">Conditions</span>
          </div> -->
          <el-form :model="form" :rules="formRules" ref="generalForm" label-position="top" label-width="100px" size="small">
            <el-form-item label="System" prop="system">
              <el-select v-model="form.system">
                <el-option
                  v-for="item in systems"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="Category" prop="category">
              <el-select v-model="form.category">
                <el-option
                  v-for="item in category"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <el-form ref="customerForm" v-show="form.category === 'customer_info'" label-position="top" label-width="100px" :model="customerForm" :rules="customerFormRules" size="small">
            <el-form-item label="Customer ID" prop="customer_id">
              <el-input v-model="customerForm.customer_id" @keyup.enter.native="onSubmit" />
            </el-form-item>
            <el-form-item label="Legal ID">
              <el-input v-model="customerForm.legal_id" @keyup.enter.native="onSubmit" />
            </el-form-item>
            <el-form-item label="Passport">
              <el-input v-model="customerForm.passport" @keyup.enter.native="onSubmit" />
            </el-form-item>
            <el-form-item label="Tax code">
              <el-input v-model="customerForm.tax_code" @keyup.enter.native="onSubmit" />
            </el-form-item>
          </el-form>
          <el-form ref="transactionForm" v-show="form.category === 'transaction'" label-position="top" label-width="100px" :model="transactionForm" :rules="transactionFormRules" size="small">
            <el-form-item label="Customer ID" prop="customer_id">
              <el-input v-model="transactionForm.customer_id" @keyup.enter.native="onSubmit" />
            </el-form-item>
            <el-form-item label="Account number" prop="account_number">
              <el-input v-model="transactionForm.account_number" @keyup.enter.native="onSubmit" />
            </el-form-item>
            <el-form-item label="From" prop="from">
              <div class="block">
                <el-date-picker
                  v-model="transactionForm.from"
                  :picker-options="pickerOptions"
                  format="dd-MM-yyyy"
                  value-format="yyyy-MM-dd"
                  type="date"
                  placeholder="Pick a day">
                </el-date-picker>
              </div>
            </el-form-item>
            <el-form-item label="To" prop="to">
              <div class="block">
                <el-date-picker
                  v-model="transactionForm.to"
                  :picker-options="pickerOptions"
                  format="dd-MM-yyyy"
                  value-format="yyyy-MM-dd"
                  type="date"
                  placeholder="Pick a day">
                </el-date-picker>
              </div>
            </el-form-item>
          </el-form>
          <el-button :disabled="loading" type="primary" size="mini" class="mt-2" @click="onSubmit">Search</el-button>
        </el-card>
      </el-col>
    </el-row>
    <result-dialog v-if="Object.keys(request).length" :request="request" />
  </div>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex'
import ResultDialog from '@/components/QueryTool/ResultDialog'
import common from '@/mixins/common.js'

export default {
  name: 'query-tool-dashboard',

  mixins: [common],

  components: {
    ResultDialog
  },

  data() {
    return {
      columnDefs: [
        { label: 'In', prop: 'request_at', width: 120 },
        { label: 'Out', prop: 'respond_at', width: 120 },
        { label: 'System', prop: 'system', width: 70 },
        { label: 'Category', prop: 'category', width: 120 },
        { label: 'Customer ID', prop: 'q.customer_id', width: 100 },
        { label: 'Account number', prop: 'q.account_number' },
        { label: 'Legal ID', prop: 'q.legal_id', width: 100 },
        { label: 'Passport', prop: 'q.passport' },
        { label: 'Tax code', prop: 'q.tax_code' },
        { label: 'Request by', prop: 'request_by', width: 100, fixed: 'right' },
        { label: 'Status', prop: 'status', align: 'center', width: 100, fixed: 'right' }
      ],
      status: {
        0: { type: 'danger', description: 'Error' },
        1: { type: 'success', description: 'Success' },
        2: { type: 'warning', description: 'Pending' },
        3: { type: 'info', description: 'Refresing' }
      },
      categories: {
        customer_info: { description: 'Customer Info', type: 'info' },
        transaction: { description: 'Transaction', type: 'danger' }
      },
      systems: [
        { 
          label: 'T24', 
          value: 'T24', 
          category: [
            { label: 'Customer Info', value: 'customer_info' },
            { label: 'Transaction', value: 'transaction' }
          ] 
        }
      ],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now() || time.getTime() < 1546300800000
        },
        shortcuts: [
          {
            text: 'Today',
            onClick(picker) {
              picker.$emit('pick', new Date())
            }
          }, 
          {
            text: 'Yesterday',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            }
          }, 
          {
            text: 'A week ago',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            }
          }
        ]
      },
      // Form options
      filterSidebar: true,
      form: {
        system: 'T24',
        category: 'customer_info'
      },
      customerForm: {
        customer_id: null,
        legal_id: null,
        passport: null,
        tax_code: null
      },
      transactionForm: {
        customer_id: null,
        account_number: null,
        from: null,
        to: null
      },
      // From rules
      formRules: {
        system: [{ required: true, message: 'Please select', trigger: 'change' }],
        category: [{ required: true, message: 'Please select', trigger: 'change' }]
      },
      customerFormRules: {
        customer_id: [{ min: 8, max: 8, message: 'Length should be 8', trigger: 'blur' }]
      },
      transactionFormRules: {
        account_number: [{ min: 14, max: 14, message: 'Length should be 14', trigger: 'blur' }],
        customer_id: [{ min: 8, max: 8, message: 'Length should be 8', trigger: 'blur' }],
        from: [{ required: true, message: 'Please select', trigger: 'change' }],
        to: [{ required: true, message: 'Please select', trigger: 'change' }]
      },
      // Requests
      q: {
        page_size: 20,
        page_number: 1,
        search_key: null
      },
      request: {}
    }
  },

  computed: {
    ...mapGetters('QueryTool', ['loading']),

    category() {
      const system = this.systems.find(s => s.value === this.form.system)
      return system ? system.category : []
    },

    generateIndex() {
      if (this.q.page_number === 1) return this.q.page_number
      else return (this.q.page_number - 1) * this.q.page_size + 1
    }
  },

  watch: {
    'form.system'() {
      this.form.category = null
    }
  },

  created() {
    this.retrieveRequest(this.q)

    this.interval = setInterval(function () {
      this.onRefresh()
    }.bind(this), 60000)
  },

  beforeDestroy() {
    clearInterval(this.interval)
  },

  methods: {
    ...mapMutations('QueryTool', ['TOGGLE_RESULT_DIALOG']),

    accessObjByString(path, obj = self, separator = '.') {
      const properties = Array.isArray(path) ? path : path.split(separator)
      return properties.reduce((prev, curr) => prev && prev[curr], obj)
    },

    toggleFilter() {
      this.filterSidebar = !this.filterSidebar
    },

    sizeChange(value) {
      this.q.page_size = value
      this.retrieveRequest(this.q)
    },

    currentPageChange(value) {
      this.q.page_number = value
      this.retrieveRequest(this.q)
    },

    onRefresh() {
      // this.q.page_number = 1
      // this.q.search_key = null
      this.retrieveRequest(this.q)
    },

    retrieveRequest(params) {
      this.$store.dispatch('QueryTool/retrieveRequest', params)
        .then(res => {
          if (!res.ok) this.$message({ message: res.data, type: 'error' })
        })
    },

    view(request) {
      this.request = request
      this.TOGGLE_RESULT_DIALOG()
    },

    onSubmit() {
      this.$refs.generalForm.validate((valid) => {
        if (valid) {
          // Validate customer info
          if (this.form.system === 'T24' && this.form.category === 'customer_info') {
            this.$refs.customerForm.validate((valid) => {
              if (valid) {
                if (this.isEmptyObj(this.customerForm)) {
                  this.$message({
                    message: 'Cần nhập ít nhất một trường dữ liệu',
                    type: 'warning'
                  })
                  return false
                }
                this.$store.dispatch('QueryTool/addRequest', { ...this.form, q: this.customerForm })
              }
            })
          } else if (this.form.system === 'T24' && this.form.category === 'transaction') { // Validate transaction
            this.$refs.transactionForm.validate((valid) => {
              if (valid) {
                if (!this.transactionForm.customer_id && !this.transactionForm.account_number) {
                  this.$message({
                    message: 'Cần nhập ít nhất một trường dữ liệu',
                    type: 'warning'
                  })
                  return false
                }
                if (this.transactionForm.from > this.transactionForm.to) {
                  this.$message({
                    message: 'Ngày bắt đầu phải nhỏ hơn ngày kết thúc',
                    type: 'warning'
                  })
                  return false
                }
                this.$store.dispatch('QueryTool/addRequest', { ...this.form, q: this.transactionForm })
              }
            })
          }
        }
      })
    }
  }
}
</script>
<style scoped>
>>>.el-table td {
  padding: 0 !important;
}
>>>.el-form--label-top .el-form-item__label {
  padding: 0;
}
>>>.icon {
  font-size: 1.2em;
}
>>>.el-select, >>>.el-date-editor.el-input {
  width: 100%
}
>>>.el-form-item--small.el-form-item {
  margin-bottom: 12px;
}
>>>.el-card__header {
  padding: 10px 20px;
}
</style>



