<template>
  <div>
    <span class="subheading font-weight-medium">Mobilize</span>
    <el-divider direction="vertical" />
    <el-link :disabled="!items.length" :underline="false" type="info" @click="exportExcel">
      Export
      <i class="el-icon-printer el-icon--right"></i> 
    </el-link>

    <el-table
      v-if="items.length"
      :data="items"
      border
      size="mini"
      class="mt-2"
      style="width: 100%">
      <el-table-column 
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :prop="c.prop"
        :align="c.align"
        :sortable="c.sortable">
        <template slot-scope="header" scope="{ column }">
          <span :title="column.label">{{ column.label }}</span>
        </template>
        <template slot-scope="scope">
          <span v-if="['SO_TIEN_NGUYEN_TE', 'SO_TIEN_QUY_DOI'].includes(c.prop)">
            {{ scope.row[c.prop] | numberWithDots }}
          </span>
          <span v-else>{{ scope.row[c.prop] }}</span>
        </template>
      </el-table-column>
    </el-table>
    <div 
      v-else 
      class="text-md-center text-sm-center text-xs-center subheading ma-2">
      No data available
    </div>
  </div>
</template>
<script>
import Export from '@/mixins/export.js'

export default {
  name: 'T24-mobilize',

  mixins: [Export],

  props: {
    items: {
      type: Array,
      default: () => []
    }
  },

  data: () => ({
    columnDefs: [
      { label: 'Mã hợp đồng', prop: 'CONTRACT_ID', width: 120 },
      { label: 'IDKH', prop: 'CUSTOMER_ID', width: 80 },
      { label: 'Tên KH', prop: 'CUSTOMERNAME_EN', width: 150 },
      { label: 'Số tiền nguyên tệ', prop: 'SO_TIEN_NGUYEN_TE', width: 130 },
      { label: 'Số tiền quy đổi', prop: 'SO_TIEN_QUY_DOI', width: 130 },
      { label: 'Lãi suất', prop: 'INTEREST_RATE', width: 80 },
      // { label: 'Kỳ hạn', prop: 'KYHAN' },
      { label: 'Loại tiền', prop: 'CURRENCY', width: 80 },
      { label: 'Ngày hiệu lực', prop: 'prop_DATE', width: 120 },
      // { label: 'Ngày quay vòng', prop: 'ROLLOVER_DATE' },
      // { label: 'Ngày đáo hạn', prop: 'MATURITY_DATE' },
      // { label: 'Ngày đáo hạn gốc', prop: 'ORIG_MAT_DATE' },
      { label: 'Phân hệ', prop: 'PHANHE', width: 80 },
      { label: 'Category', prop: 'CAT_DESCRIPTION' },
      { label: 'Sản phẩm', prop: 'PROD_DESCRIPTION' },
      { label: 'Chi nhánh', prop: 'MNE', width: 130 }
      // { label: 'Vùng', prop: 'VUNG' },
      // { label: 'Miền', prop: 'MIEN' }
    ]
  }),

  methods: {
    exportExcel() {
      const fields = this.columnDefs.map(e => e.prop)
      const params = {
        header: fields,
        filterVal: fields,
        filename: `MOBILIZE-${this.items[0].CUSTOMER_ID}`,
        items: this.items
      }
      this.handleDownload(params)
    }
  }
}
</script>



