<template>
  <div>
    <span class="subheading font-weight-medium">Basic Information</span>
    <el-divider direction="vertical" />
    <el-link v-if="items.length" :underline="false" type="info" @click="exportExcel">
      Export
      <i class="el-icon-printer el-icon--right"></i> 
    </el-link>

    <el-table
      :data="items"
      border
      size="mini"
      class="mt-2"
      style="width: 100%">
      <el-table-column 
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :prop="c.prop"
        :align="c.align"
        :sortable="c.sortable">
        <template slot="header" scope="{ column }">
          <span :title="column.label">{{ column.label }}</span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import Export from '@/mixins/export.js'

export default {
  name: 'T24-customer',

  mixins: [Export],

  props: {
    items: {
      type: Array,
      default: () => []
    }
  },

  data: () => ({
    columnDefs: [
      { label: 'IDKH', prop: 'IDCUS' },
      { label: 'Tên KH', prop: 'CUSNAME_VIE', width: 150 },
      { label: 'ĐC liên lạc', prop: 'ADDRESS', width: 220 },
      { label: 'ĐC thường trú', prop: 'ADDRESS_CMT', width: 220 },
      { label: 'Ngày sinh', prop: 'BIRTH', width: 100 },
      { label: 'CMT', prop: 'CMT', width: 100 },
      { label: 'Hộ chiếu', prop: 'HOCHIEU' },
      { label: 'CMT cũ', prop: 'CMT_CU' },
      { label: 'Hộ chiếu cũ', prop: 'HOCHIEU_CU' },
      { label: 'MST', prop: 'MST' },
      { label: 'SĐT', prop: 'TEL1' },
      { label: 'SĐT cũ', prop: 'TEL1_CU', width: 100 },
      { label: 'Trạng thái', prop: 'CUSTOMER_STATUS' },
      { label: 'Chương trình', prop: 'CHTRINH' },
      { label: 'Priority', prop: 'PHANLOAI' },
      { label: 'Ngày hết hạn', prop: 'NGAYKETTHUC' },
      { label: 'CN định danh', prop: 'COMPANY_BOOK' }
    ]
  }),

  methods: {
    exportExcel() {
      const fields = this.columnDefs.map(e => e.prop)
      const params = {
        header: fields,
        filterVal: fields,
        filename: `CUSTOMER-${this.items[0].IDCUS}`,
        items: this.items
      }
      this.handleDownload(params)
    }
  }
}
</script>



